.dashboardProjects{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: 160px;
    gap:20px;
    margin-top: 16px;
}

.dashboardProjects a{
    color: #efefef;
    text-decoration: none;
}

.dproject{
  
    padding: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
-moz-box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);

transition: all 0.3s ease-in-out;
}

.dproject:hover{
    background-color: #3e3ebe;
  
    transition: all 0.3s ease-in-out;

}

.dueDate{
    opacity: 0.85;
}

.dproject h3{
    margin:10px 0px 0px 0px;
    font-size: 22px;
    color:#eef0fd;
}


.dproject p{
    margin:8px 0px 0px 0px;
    font-size: 14px;
    color:rgb(174, 167, 167);
}


.assignedUser ul{
    display: flex;
    flex-direction: row;
    margin-top: 22px;
    position: relative;
}

.assignedUser ul::before{
    position: absolute;
    content:'';
    top:-10px;opacity: 0.1;
    left:5px;
    width: 280px;
    height: 2px;
    background: #ffffff;
}

.assignedUser ul li{
    margin: 2px;
}