.onlineUsers{
    background-color:#eef0fd;
    overflow-y: hidden;
    overflow-x: hidden;
    display: grid;
    grid-template-rows: 10% 90%;
    -webkit-box-shadow: -2px 10px 11px -8px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 10px 11px -8px rgba(0,0,0,0.75);
    box-shadow: -2px 10px 11px -8px rgba(0,0,0,0.75);

}

.onlineUsers .heading img{
    max-width: 100px;
    max-height: 100px;
    display: inline;
}



.onlineUsers h3{
    display: inline;
    color:#8478fa;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 2px;
    text-align: center;
    padding:10px 0px;
    border-bottom-left-radius: 20px;
}

.heading{
    background-color: #eef0fd;
    border-bottom-right-radius: 30px;
    width: 100%;
    height: 100%;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
}




#users{
    display: flex;
    
    align-items: center;
    font-family: 'Roboto', sans-serif;
    justify-content: space-around;
    color:#eef0fd;
    background-color: #1a1f3f;
    -webkit-box-shadow: 3px 3px 8px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 3px 8px -1px rgba(0,0,0,0.75);
    box-shadow: 3px 3px 8px -1px rgba(0,0,0,0.75);
    
    
    
    animation: onlineUsers;
    animation-duration: 1s;
    animation-delay: 2s;
    opacity: 0;
    animation-fill-mode: forwards;
}

.online{
    border-left:6px solid #3e3ebe;
    border-radius: 5px;
    margin-top: 25px;
}

.offline{
    border-left:6px solid #eef0fd;
    border-radius: 5px; margin-top: 25px;
}


@keyframes onlineUsers {
    0%{
    
        opacity: 0;
    }
    100%{

        opacity: 1;
    }
}




.bg_head{
    background-color: #1a1f3f;

}

.userContainer{
    background-color: #1a1f3f;
    height: 100%;
    

    border-top-left-radius: 30px;
}

