.sidebar{
    /* background-color: #1a1f3f; */
    background-color:#eef0fd;
    display: grid;
    grid-template-rows: 27% 73%;
    height: 100vh;
  position: relative;
    -webkit-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    color:#eef0fd;
    text-align: center;
    overflow-y: hidden;
}

.bg1{
    background-color: #1a1f3f;
}

.sidebarUser
{

    justify-items: start;
    background-color: #eef0fd;
    border-bottom-right-radius: 30px;
   
}

.sidebarUser span p{ 
    color:#3e3eb7;
    font-weight: 400;
    margin: 10px 0px;
    letter-spacing: 1px;
    font-family: 'Bebas Neue', cursive;
}

.sidebarUser h2{

    letter-spacing: 2px;
}



.sidebarLinks{
    background-color: #1a1f3f;
    border-top-left-radius: 30px;

}

.slink{
    margin-top: 30px;
    padding-left: 10px;
    display: flex;
    align-items: center;
    gap:5px;
    padding:5px;
    border-radius: 20px 0px 0px 20px;
}

.slink img{
    max-width: 20px;
    max-height: 20px;
    color: #eef0fd;
}

.sidebarLinks a{
    color:#eef0fd;
    text-decoration: none;
    border:none;
}

.active{
    background-color: #3e3eb7;
}



.dummy{
    position: absolute;
    background-color: #1a1f3f;
    opacity: 1;
    width: 100%;
    height: 50%;
    top:0px;
    animation: Sidebar;
    animation-duration: 3s;
    animation-delay: 2s;
    animation-fill-mode: forwards;

}
@keyframes Sidebar {
    0%{
        z-index: 1;
        opacity: 1;
    }
    100%{

        opacity: 0;
        z-index: -1;
    }
}