.wholeProject{
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 30px;
}

.aboutProject{
  
    border-radius: 10px;
    height:  fit-content;
    padding: 14px;
}

.aboutProject .dueDate{
    font-size: 15px;

    color: rgb(174, 167, 167);
    opacity: 0.85;
    margin-bottom: 16px;
}

.projectTitle{
    color:#eef0fd;
    font-size: 25px;
    margin: 0px 0px 10px 0px ;

}

.projectDetail{
    color:#eef0fd;
    font-size: 18px;
}

.projectAssign{
    margin: 24px 0px;
}

.projectAssign p{
    margin: 10px 4px;
    font-size: 18px;
    color: #3e3ebe;
    font-weight: 600;
}

.projectAssign ul{
    display: flex;
}

.chat h2{
    color:#3e3ebe;
    margin: 10px 0px;
    padding: 0px 28px;
}

.chat form{
    padding: 10px 16px;
    border-radius: 10px;
    
    /* -webkit-box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
-moz-box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75); */
}

.chat form li{
    display: flex;
    flex-direction: column;
    margin: 14px 0px;
    padding: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
-moz-box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
box-shadow: 6px 6px 7px -4px rgba(0,0,0,0.75);
}

.chat form p{
    color:#eef0fd;
    
}

.chat .aboutUser{
    display: flex;
    align-items: center;

}

.chat .dueDate{

    font-size: 12px;
    padding: 3px 10px;
    color: rgb(174, 167, 167);
    opacity: 0.85;
}

.chat label textarea{
    background-color: #1a1f3f;
    /* margin:10px; */
    outline: none;
    height:24px;
    width: 380px;
    font-size: 18px;
    outline: none;
    border: none;
    
    padding: 5px;
    color:#eef0fd;
    border-radius: 8px;
}

.chat .chatBox{
    display: flex;
    align-items: center;
    justify-content: center;
    border:1px solid #eef0fd;
    border-radius: 8px;
}

.chat .userComment{
    padding: 10px 4px;
  
}

.chatBox button{
    background:none;
    border: none;
    outline: none;
}

.chatBox  img{
    max-width: 30px;
    max-height: 30px;
    background-color: none;
    border: none;
    color:#eef0fd;
    outline: none;
}


.mark {
    text-decoration: none;
    color:#eef0fd;
    border: 1px solid #eef0fd;
    padding: 8px;

    border-radius: 6px;

}

.mark :hover{
    color:#3e3ebe;
    background: #eef0fd;
}