.addProjects{
    max-width: 610px;
    background-color: #3e3eb7;
    padding: 10px 0px 0px 0px;
    border-radius: 10px;
    color:#eef0fd;
    font-family: 'Roboto', sans-serif;

    margin:30px 80px; 
}


.addProjects form{
    position: relative;
    background-color: #1a1f3f;
    padding:20px;
    margin-top:22px;
   



    -webkit-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
    -moz-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
    box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
}

