.user-img img{
    display: inline-block;
    width: 45%;
    margin-top: 10px;
   border-radius: 10px;
   padding: 10px;
   border:none;
   outline: none;
   background-color: #ffffff;   
   -webkit-box-shadow: 1px 7px 40px -22px rgba(0, 0, 0, 1);
-moz-box-shadow: 1px 7px 40px -22px rgba(0, 0, 0, 1);
box-shadow: 1px 7px 40px -22px rgba(0, 0, 0, 1);

}

.onlineUser img{
max-width: 40px;
max-height: 40px;
border:1px solid #eef0fd;
border-radius: 100%;
padding:2px;
margin:0px 4px;
}

.userChat img{
    max-width: 30px;
max-height: 30px;
border-radius: 100%;
padding:2px;
margin:0px 4px;
}