.login{
    background-color: #3e3eb7;
    padding: 10px 0px 0px 0px;
    border-radius: 10px;
    color:#eef0fd;
    width:40%;
    font-family: 'Roboto', sans-serif;
    margin: 0 auto;
    margin-top:50px;
}

.login form{
    background-color: #1a1f3f;
    padding:60px 40px 40px 60px;
    margin-top:22px;
   
    text-align: center;


    -webkit-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
    -moz-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
    box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
}

.login form label{
    font-size: 20px;

}

.login form input{
    font-size: 16px;
    outline: none;
    padding: 10px;
    color:#eef0fd;
    width: 80%;
    border-radius: 8px;
    background-color: #1a1f3f;
    border:1px solid #eef0fd;
    margin: 12px 0px;
}

.login form button{
    font-size: 16px;
    background-color: #3e3eb7;
outline:none;
border: none;
color:#eef0fd;
    padding:8px 10px;
    border-radius: 4px;
    display: block;
    width: 20%;
    margin:0 auto;
    margin-top: 40px;


}


.login img{
    max-width: 90px;
    max-height: 90px;
    border-radius: 100%;
    background-color: #3e3eb7;
    padding: 12px;
    position: absolute;
    -webkit-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
-moz-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
left:630px;
top:90px;
}
