.signupc{
    background-color: #3e3eb7;
    font-family: 'Roboto', sans-serif;

    padding: 20px 0px 0px 0px;
    border-radius: 10px;
    color:#eef0fd;
    width:40%;
    margin: 0 auto;
    margin-top: 40px;
    margin-bottom: 40px;
}

.signupc form{
    background-color: #1a1f3f;
    padding:60px 40px 40px 60px;
    margin-top:14px;
    
    text-align: center;


    -webkit-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
    -moz-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
    box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
}

.signupc form label{
    font-size: 18px;
    
}

.signupc form input{
    font-size: 16px;
    outline: none;
    padding: 10px;
    color:#eef0fd;
    width: 80%;
    background-color: #1a1f3f;
    border:1px solid #eef0fd;
    margin: 16px 0px 20px 0px;
    border-radius: 8px;
}

.signupc form button{
    font-size: 16px;
    background-color: #3e3eb7;
outline:none;
border: none;
color:#eef0fd;
    padding:8px 10px;
    border-radius: 4px;
    display: block;
    width: 20%;
    margin:0 auto;
    margin-top: 40px;


}


.signupc img{
    max-width: 90px;
    max-height: 90px;
    border-radius: 100%;
    background-color: #3e3eb7;
    padding: 12px;
    top:86px;
    position: absolute;
    -webkit-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
-moz-box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
box-shadow: 9px 9px 21px -15px rgba(0, 0, 0, 1);
left:630px;
}

