
.navbar{
    width:100vw;
    background-color: #1a1f3f;

    height: fit-content;
    margin-bottom: 25px;
   
}

.navbar ul{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    -webkit-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
   
}

.navbar ul li{
    background-color: none;
    list-style: none;
    display: flex;
    align-items: center;
    text-align: center;
}

.navbar ul a{

    text-decoration: none;
    margin: 0px 20px;
    color:#eef0fd;
padding:8px 0px 10px 0px;
border-radius: 2px;

}


.navbar .logoc img{
    max-width: 140px;
    max-height: 140px;
    
}




.navbar ul li img{
    max-width: 40px;
    max-height: 40px;
}

.navbar ul div{
    display: flex;
    justify-content: space-between;
    gap:20px;
}

.navbar button{
background-color: #3e3eb7;
outline:none;
border: none;
color:#eef0fd;
padding:10px 12px;
border-radius: 4px;

}

.navbar .active{
    border-bottom: 1px solid #eef0fd;
    background-color: #1a1f3f;
}