.filter{
    margin: 16px 0px 24px 0px;
    -webkit-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    -moz-box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
    box-shadow: 3px 1px 11px -7px rgba(0,0,0,0.75);
padding: 10px;
font-size: 18px;
color:#eef0fd;
}

.filter p{
    display: inline;
    padding: 0px 10px;
}

.filter button{
    padding: 7px;
    border-radius: 5px;
    margin: 0px 5px;
    background-color: #1a1f3f;
    color:#eef0fd;
    z-index: 100;
    border: 1px solid #eef0fd;
    outline: none;
    
}

.filter .activeBtn{
    background-color: #3e3eb7;
}